/**
 * A Staff Member represents a person with predefined responsibilities for operating events.
 *
 * You can set each staff member's permissions and assign them to some or all events on a site.
 *
 * Upon creation, Wix automatically generates a login link for the staff member to the Check-in app, where they can perform their roles without interacting with the site itself. Learn more about [Check-in](https://support.wix.com/en/article/check-in-by-wix-an-overview).
 */
export interface StaffMember extends StaffMemberEventAccessOneOf {
  /** Generate link for all events on a site. If a staff member is assigned to all events on a Wix site, they're automatically assigned to every new event too. */
  allEvents?: AllEvents;
  /** Generate link for the specified events. */
  specificEvents?: SpecificEvents;
  /**
   * Staff member ID.
   * @format GUID
   * @readonly
   */
  id?: string | null;
  /**
   * Revision number, which increments by 1 each time the staff member is updated. To prevent conflicting changes, the existing `revision` must be used when updating a staff member.
   * @readonly
   */
  revision?: string | null;
  /**
   * Date and time the staff member was created.
   * @readonly
   */
  createdDate?: Date | null;
  /**
   * Date and time the staff member was updated.
   * @readonly
   */
  updatedDate?: Date | null;
  /**
   * Staff member name.
   * @minLength 1
   * @maxLength 120
   */
  name?: string | null;
  /** Staff member status. To change the status, call Update Staff Member. */
  status?: StatusWithLiterals;
  /** Data extensions. */
  extendedFields?: ExtendedFields;
  /**
   * Staff member permissions.
   * @maxSize 4
   */
  permissions?: PermissionWithLiterals[];
  /**
   * Login link generated for the staff member. This link opens up the [Check-in](https://support.wix.com/en/article/check-in-by-wix-an-overview) app.
   * @format WEB_URL
   * @readonly
   */
  loginLink?: string | null;
}

/** @oneof */
export interface StaffMemberEventAccessOneOf {
  /** Generate link for all events on a site. If a staff member is assigned to all events on a Wix site, they're automatically assigned to every new event too. */
  allEvents?: AllEvents;
  /** Generate link for the specified events. */
  specificEvents?: SpecificEvents;
}

export enum Scope {
  UNKNOWN_SCOPE = 'UNKNOWN_SCOPE',
  /** Check in guests. */
  CHECK_IN_GUESTS = 'CHECK_IN_GUESTS',
  /** Sell tickets. */
  SELL_TICKETS = 'SELL_TICKETS',
  /** Add guests. */
  ADD_GUESTS = 'ADD_GUESTS',
  /** Read analytics. */
  READ_ANALYTICS = 'READ_ANALYTICS',
}

/** @enumType */
export type ScopeWithLiterals =
  | Scope
  | 'UNKNOWN_SCOPE'
  | 'CHECK_IN_GUESTS'
  | 'SELL_TICKETS'
  | 'ADD_GUESTS'
  | 'READ_ANALYTICS';

export interface AllEvents {}

export interface SpecificEvents {
  /**
   * List of events for which staff members have access.
   * @format GUID
   * @maxSize 50
   */
  events?: string[];
}

export enum Status {
  /** The staff member hasn't logged in yet. */
  PENDING = 'PENDING',
  /** The staff member is logged in and has access to an event. */
  HAS_ACCESS = 'HAS_ACCESS',
  /** The access to an event is paused for the staff member. */
  ACCESS_PAUSED = 'ACCESS_PAUSED',
}

/** @enumType */
export type StatusWithLiterals =
  | Status
  | 'PENDING'
  | 'HAS_ACCESS'
  | 'ACCESS_PAUSED';

export interface ExtendedFields {
  /**
   * Extended field data. Each key corresponds to the namespace of the app that created the extended fields.
   * The value of each key is structured according to the schema defined when the extended fields were configured.
   *
   * You can only access fields for which you have the appropriate permissions.
   *
   * Learn more about [extended fields](https://dev.wix.com/docs/rest/articles/getting-started/extended-fields).
   */
  namespaces?: Record<string, Record<string, any>>;
}

export enum Permission {
  /** Unknown permission. This value isn't used. */
  UNKNOWN_PERMISSION = 'UNKNOWN_PERMISSION',
  /** The staff member can view the guest list, check in guests, and scan tickets. */
  CHECK_IN_GUESTS = 'CHECK_IN_GUESTS',
  /** The staff member can sell tickets. */
  SELL_TICKETS = 'SELL_TICKETS',
  /** The staff member can add guests to the event manually and mark their payment status. */
  ADD_GUESTS = 'ADD_GUESTS',
  /** The staff member can track tickets sales, site visits, and trends. */
  READ_ANALYTICS = 'READ_ANALYTICS',
  /** The staff member can see the guest's form answers. */
  READ_GUEST_FORM = 'READ_GUEST_FORM',
}

/** @enumType */
export type PermissionWithLiterals =
  | Permission
  | 'UNKNOWN_PERMISSION'
  | 'CHECK_IN_GUESTS'
  | 'SELL_TICKETS'
  | 'ADD_GUESTS'
  | 'READ_ANALYTICS'
  | 'READ_GUEST_FORM';

